body{
  /* background */
  color:white;
}
*{
  margin:0;
  padding:0;
  text-decoration: none;
}
li{
  list-style:none;
}
a{
  text-decoration: none;
}
img{
  vertical-align: middle;
}
.Wrap{
  background:#1e2238;
}
.innerBox{
  max-width:1200px;
  margin:0 auto;
}
header{
  border-bottom: 1px solid #40375a;
}
header>.innerBox{
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
.logo{
  width:80px;
}
.logo img {
  width: 100%;
}
#nav{
  display:flex;
}
#nav > li {
  margin:35px;
}
#nav > li > a{
  color:#eee;
}

